import { Slot } from '@radix-ui/react-slot';
import { clsx } from 'clsx';
import { ReactNode } from 'react';

interface HeadingProps {
    size?: 'sm' | 'md' | 'lg';
    children: ReactNode;
    asChild?: boolean;
}

function Heading({ size = 'md', children, asChild }: HeadingProps) {
    const Component = asChild ? Slot : 'h2';

    return (
        <Component className={
            clsx('text-gray-100 font-bold font-sans', 
                {
                    'text-lg': size === 'sm',
                    'text-xl': size === 'md',
                    'text-2xl': size === 'lg',
                }
            )
        }>
            { children }
        </Component>
    )
}

export {
    Heading,
}

export type {
    HeadingProps,
}